.product-page-main {
  padding: 30px;
  .owl-item {
    .item {
      border: 1px solid $light-color;
      border-radius: 5px;
      background-color: #f8f8f8;
    }
    &.current {
      .item {
        border: 1px solid var(--theme-deafult);
      }
    }
  }
  .product-slider {
    margin-bottom: 20px;
    img {
      @media (min-width: 1400px) {
        height: 520px;
        object-fit: cover;
      }
      @media (min-width: 1500px) {
        height: 400px;
        object-fit: cover;
      }
    }
  }
  .product-color {
    margin-top: 10px;
  }
}

.product-page-details {
  h4 {
    color: $theme-body-font-color;
    text-transform: uppercase;
    font-size: 18px;
  }

  span {
    padding-left: 15px;
  }
}

.br-theme-fontawesome-stars {
  .br-widget {
    a,
    a.br-selected:after,
    a.br-active:after {
      color: #ffa800 !important;
      font: normal normal normal 14px/1 FontAwesome;
    }
  }
}

.product-price {
  font-size: 18px;
  font-weight: 700;
  margin-top: 5px;
  color: var(--theme-deafult);

  del {
    color: rgba($dark-color, 0.4);
    padding-left: 10px;
    font-size: 80%;
  }
}

.product-color {
  li {
    display: inline-block;
    border-radius: 100%;
    height: 20px;
    width: 20px;
    margin: 0 2px;
  }
}

.product-page-main {
  p {
    font-size: 15px;
    color: $theme-body-font-color;
  }

  .nav-link {
    text-transform: uppercase;
    font-weight: 500;
  }
}

/**====== Product  Ends ======**/

.payment-opt {
  li {
    display: inline-block;

    img {
      height: 20px;
      margin: 15px 15px 0 0;
    }
  }
}

/**=====================
    33. Ecommerce  CSS End
==========================**/
@media screen and (max-width: 1660px) and (min-width: 1200px) {
  .owl-carousel .owl-item img {
    max-width: 420px;
    margin: 0 auto;
  }
}
